          let conversationId = conversation.id;


          if(document.getElementById("chat_" + conversationId)===null)
          {
              //* This code will add new <chat-detail> component. The <chat-detail> component loads teh chat data on its mounted-method * //
              this.openedChats.push({
                  conversationId : conversationId,
                  conversationName : conversation.name,
                  categoryId : conversation.categoryId,
                  showChat : true
              });
          }

          {
              this.openedChats.forEach((chat) => {
                  let conversationId = chat.conversationId;
                  if(document.getElementById("chat_" + conversationId)!==null)
                      document.getElementById("chat_" + conversationId).classList.add("hideChat");
              });

              if(document.getElementById("chat_" + conversationId)!==null)
                  document.getElementById("chat_" + conversationId).classList.remove("hideChat");
          }

          if(document.querySelector(".selected_chat_itemX")!==null)
              document.querySelector(".selected_chat_itemX").classList.remove("selected_chat_itemX");

          if(document.getElementById("conf_list_item_" + conversationId)!==null)
