export default{

        methods : {
            convertUTCDateFromServerToLocalDateForDisplay(utcDateString){

                  let utcFormattedString = utcDateString.replace(',', '').replace('T',' ') + " UTC";
                  let localDateString = new Date(utcFormattedString).toLocaleString();

                  //Convert from "01/05/2021, 09:00:00" to "2021-05-01T03:36:00";
                  let dateString = localDateString.split(",")[0];
                  let timeString = localDateString.split(",")[1].trim();

                  return dateString.split("/")[2] + "-" +
                         dateString.split("/")[1] + "-" +
                         dateString.split("/")[0] + " " +
                         timeString;

            },
            convertUTCDateFromServerToLocalDate(utcDateString){

                  let utcFormattedString = utcDateString.replace(',', '').replace('T',' ') + " UTC";
                  let localDateString = new Date(utcFormattedString).toLocaleString();

                  //Convert from "01/05/2021, 09:00:00" to "2021-05-01T03:36:00";
                  let dateString = localDateString.split(",")[0];
                  let timeString = localDateString.split(",")[1].trim();

                  return dateString.split("/")[2] + "-" +
                         dateString.split("/")[1] + "-" +
                         dateString.split("/")[0] + "T" +
                         timeString;

            },
            checkIfMobile(){

            let ua = navigator.userAgent,
                browser = /Edge\/\d+/.test(ua) ? 'ed' : /MSIE 9/.test(ua) ? 'ie9' : /MSIE 10/.test(ua) ? 'ie10' : /MSIE 11/.test(ua) ? 'ie11' : /MSIE\s\d/.test(ua) ? 'ie?' : /rv\:11/.test(ua) ? 'ie11' : /Firefox\W\d/.test(ua) ? 'ff' : /Chrom(e|ium)\W\d|CriOS\W\d/.test(ua) ? 'gc' : /\bSafari\W\d/.test(ua) ? 'sa' : /\bOpera\W\d/.test(ua) ? 'op' : /\bOPR\W\d/i.test(ua) ? 'op' : typeof MSPointerEvent !== 'undefined' ? 'ie?' : '',
                os = /Windows NT 10/.test(ua) ? "win10" : /Windows NT 6\.0/.test(ua) ? "winvista" : /Windows NT 6\.1/.test(ua) ? "win7" : /Windows NT 6\.\d/.test(ua) ? "win8" : /Windows NT 5\.1/.test(ua) ? "winxp" : /Windows NT [1-5]\./.test(ua) ? "winnt" : /Mac/.test(ua) ? "mac" : /Linux/.test(ua) ? "linux" : /X11/.test(ua) ? "nix" : "",
                touch = 'ontouchstart' in document.documentElement,
                mobile = /IEMobile|Windows Phone|Lumia/i.test(ua) ? 'w' : /iPhone|iP[oa]d/.test(ua) ? 'i' : /Android/.test(ua) ? 'a' : /BlackBerry|PlayBook|BB10/.test(ua) ? 'b' : /Mobile Safari/.test(ua) ? 's' : /webOS|Mobile|Tablet|Opera Mini|\bCrMo\/|Opera Mobi/i.test(ua) ? 1 : 0,
                tablet = /Tablet|iPad/i.test(ua);

            //console.log("ua = " + ua);
            //console.log("browser = " + browser);
            //console.log("os = " + os);
            //console.log("touch = " + touch);
            //console.log("mobile = " + mobile);
            //console.log("tablet = " + tablet);

            this.isMobileDevice = mobile !==0;

          },
            doNetworkSpeedTest : function(){

                    var src = "images/pexels-photo-257923.jpeg",
                        size = 2259489,	// ~= 2.2mb
                        image = new Image(),
                        startTime,
                        endTime,
                        totalTime = 0,
                        speed = 0,
                        connectivity_speed=0;

                    image.onload = function () {
                              endTime = (new Date()).getTime();
                              totalTime = ((endTime - startTime) / 1000).toFixed(1);
                              speed = ((size / totalTime)/1024).toFixed(0);; // bytes per second
                              connectivity_speed = ((size/totalTime)/1024).toFixed(0);

                                if(speed > 1024)
                                {
                                      speed = (speed/1024).toFixed(1) + "mbps";

                                }
                                else
                                {
                                      speed = speed  + "kbps";
                                }

                            if(connectivity_speed > 1024)
                            {
                                $("#speed_indicator").attr("title", "Your Internet speed is GOOD.");
                                $("#speed_indicator").removeClass("default").removeClass("orange").removeClass("red").removeClass("yellow").addClass("green");
                            }
                            else if(connectivity_speed > 500 && connectivity_speed < 1024)
                            {
                                $("#speed_indicator").attr("title", "Your Internet speed is MODERATE.");
                                $("#speed_indicator").removeClass("default").removeClass("orange").removeClass("red").removeClass("green").addClass("yellow");
                            }
                            else if(connectivity_speed > 250 && connectivity_speed < 500)
                            {
                                $("#speed_indicator").attr("title", "Your Internet speed is POOR.");
                                $("#speed_indicator").removeClass("default").removeClass("yellow").removeClass("red").removeClass("green").addClass("orange");
                            }
                            else if(connectivity_speed < 250)
                            {
                                $("#speed_indicator").attr("title", "Your Internet speed is VERY POOR.");
                                $("#speed_indicator").removeClass("default").removeClass("orange").removeClass("yellow").removeClass("green").addClass("red");
                            }

                    };

                    startTime = (new Date()).getTime();
                    image.src = src + "?cacheBust=" + startTime;


            },
            enableHTMLElement : function(elem)
            {
                elem.removeAttribute("disabled");
            },
            disableHTMLElement : function(elem)
            {
                elem.setAttribute("disabled","disabled");
            },
            logOrange : function(msg)
        		{
        			console.log('%c ' + msg,  'background: orange; color: white');
        		},
        		logBlue : function(msg)
        		{
        			console.log('%c ' + msg,  'background: #03A9F4; color: white');
        		},
        		logGreen : function(msg)
        		{
        			console.log('%c ' + msg,  'background: #8BC34A; color: white');
        		},
            log2Strings : function(str1,str2){
              console.log(`%c ${str1} %c ${str2} %c`,
                "background:#35495e ; padding: 1px; border-radius: 3px 0 0 3px;  color: #fff",
                "background:#03a9f4 ; padding: 1px; border-radius: 0 3px 3px 0;  color: #fff",
                "background:transparent");
            },
            log2StringsError : function(str1,str2){
              console.log(`%c ${str1} %c ${str2} %c`,
                "background:#35495e ; padding: 1px; border-radius: 3px 0 0 3px;  color: #fff",
                "background:#e91e63 ; padding: 1px; border-radius: 0 3px 3px 0;  color: #fff",
                "background:transparent");
            }

        }

}
