                        _this.my_conference_list = (dataResponse.body["bean"]);//.slice(0, 10);

                        _this.my_conference_list.forEach(conf => {

                            conf["name_str"] = conf.name;
                            if(conf.name.length>15)
                            {
                              //conf.name = conf.name.substr(0,15) + "..";
                            }

                            if(conf.scheduledDateTime !== null)
                            {
                                conf.scheduledDateTime = new Date(conf.scheduledDateTime).toLocaleString()
                                                                                         .slice(0, 17)
                                                                                         .replace(',', '')
                                                                                         .replace('T',' ')
                                                                                         .split(" ")[1];
                            }

                            if (conf.participantList !== null)
                                conf.participants_info = conf.participantList;
                            else
                                conf.participants_info = [];


                            if(conf.isLive!==null && conf.isLive===1)
                            {
                                conf.isLive = "LIVE";
                            }
                        });

                        _this.my_conference_list.forEach(conf => {

                            let participant_names = [];
                            let participants_info = conf.participants_info;
                            participants_info.forEach(participant => {
                                participant_names.push(participant.firstName + " " + participant.lastName);
                            });


                            let participants_info_str = participant_names.join(", ");
                            conf["participants_info_str"] = participants_info_str;
                        });

                        if(_this.my_conference_list.length>0)
                        {
                          setTimeout(()=>{
                            _this.openChat(_this.my_conference_list[0].id);
                          },200);
                        }


                    } else {
                        _this.conf_service_error = dataResponse;
                        console.error("Error", dataResponse);
                        alert("Error : " + dataResponse)
                    }


                })
                .catch(function(errorResponse) {

