        getConferenceList: function() {

            let _this = this;
            this.conference_list = [];
            let searchQuery = (this.searchQuery!=="")?this.searchQuery:"all";

            let encodedUserInfo = this.getCookie("userInfo");
            let decodedUserInfoString = decodeURIComponent(encodedUserInfo);
            let userInfo = JSON.parse(decodedUserInfoString);
            let userID = userInfo["uuid"];


            let get_url = "./conference/list/1/" + searchQuery; // + userID;
            console.log("Retrieving data from : " + get_url);

            // VueJS ajax call-1
            this.$http.get(get_url)
                .then(function(dataResponse) {

                    console.log("Conference Registration Result : ");
                    console.log(dataResponse);
                    console.log(dataResponse.body["bean"]);
                    console.log((dataResponse.body["bean"]).slice(0, 5));

                    if (dataResponse.body["actionResult"] === 1) {
                        _this.conference_list = (dataResponse.body["bean"]);//.slice(0, 10);

                        _this.conference_list.forEach(conf => {

                            if(conf.scheduledDateTime !== null)
                            {
                                conf.scheduledDateTime = new Date(conf.scheduledDateTime).toLocaleString()
                                                                                         .slice(0, 17)
                                                                                         .replace(',', '')
                                                                                         .replace('T',' ');
                            }

                            if (conf.participantList !== null)
                                conf.participants_info = conf.participantList;
                            else
                                conf.participants_info = [];


                            if(conf.isLive!==null && conf.isLive===1)
                            {
                                conf.isLive = "LIVE";
                            }
                        });

                        _this.conference_list.forEach(conf => {

                            let participant_names = [];
                            let participants_info = conf.participants_info;
                            participants_info.forEach(participant => {
                                participant_names.push(participant.firstName + " " + participant.lastName);
                            });


                            let participants_info_str = participant_names.join(", ");
                            conf["participants_info_str"] = participants_info_str;
                        });

                    } else {
                        console.error("Error", dataResponse);
                        alert("Error : " + dataResponse)
                    }


                });

        },
