<template>

  <div style="display: flex;flex-grow: 1;overflow-y: hidden;position: relative;padding:0px;">
    <div style="display: flex;overflow-y:hidden;flex-direction: column;flex-grow: 1;">
        <div class="task-list" style="display: flex;justify-content: center;overflow-y:hidden;flex-grow: 1;margin-top: 10px;grid-template-rows: 1fr;background-color:#f3f6f9">
            <div class="adk_grid_list_content custom-scroll-bar" style="display: flex;flex-direction: column;row-gap: 20px;;margin-bottom: 10px;">

              <template v-for="notification,idx in notificationsList">

                    <div v-bind:class="{ 'notification_box' : true}" v-bind:id="'notification_' + notification.id" style="position:relative">
                        <div v-bind:class="{'new_notification': notification.hide, 'notification_description' : true}" >

                            <div style="flex-grow: 1;width: 100%;">
                                <div class="list-item-row-1" style="display: flex;column-gap: 5px;border-bottom: 1px solid #e8e8e8;padding: 5px;flex-grow: 1;width: 95%;">
                                    <div style="font-weight:bold;flex-grow:1;font-size: 0.75rem;">
                                      {{notification.title}}
                                      <span v-if="notification.isNew!==undefined && notification.isNew===true" style="margin-left: 5px;color: #fdfdfd;background: #58af5b;font-size: 0.55rem;padding: 1px 5px;border-radius: 3px;top: 0px;right: 0px;max-width: 15px;height: 10px;text-align: center;user-select: none;">New</span>
                                    </div>
                                </div>
                                <div style="display: flex;column-gap: 10px;margin-top: 15px;padding-top: 5px;border-left: 0px solid coral;padding: 5px 10px;border-radius: 0px;">
                                    <div style="white-space: pre-wrap;word-break: break-word;font-size: 0.75rem;">{{notification.description}}</div>
                                </div>
                                <div style="font-weight: normal;font-size: 0.55rem;;color: #b3b3b3;padding:15px 0px 5px">{{notification.createdOn}}</div>
                            </div>
                        </div>
                    </div>

              </template>
              <div id="isNotificationsListIntersectionObserver"
                   class="ui_grid_row"
                   style="margin: 10px;padding: 7px 12px; border-bottom: 0em solid rgb(229, 229, 229); display: grid; place-items: center; background: rgb(255, 255, 255); color: rgb(158, 158, 158);">
                <span style="">No more notifications.</span>
              </div>
            </div>
        </div>
    </div>
  </div>
</template>



<script>

  import {Vue} from 'vue';
  import {bus} from './../../../config_app.js';
  import uiListMixinLib from './../../../mixins/lib/ui-list.js';
  import userMixinLib from './../../../mixins/lib/user_mixin_lib.js';

  export default {
    mixins : [uiListMixinLib, userMixinLib],
    data: function() {
      return {
          api : "notifications",
          isMobileDevice: true,
          resizeTimeout: null,
          appName: this.$route.params.app_name,
          searchQuery: '',

          resetTime : {
              time : new Date().getTime()
          },

          notificationsList : [],
          currentPageNumberOfNotifications : 1,
          isNotificationListIntersectionObserverFired : false,
          allNotificationsFetched : false,

      }
    },
    methods: {

      fetchNotifications(){

          // If all records are fetched, make no calls to the server again.
          if(this.allNotificationsFetched)
            return false;

          let url = "./notifications/list/"  + this.currentPageNumberOfNotifications + "/all";
          try
          {
              // VueJS ajax call-1
              axios.get(url)
                  .then((dataResponse) => {
                        console.log("Notification List: " , dataResponse);
                        let list = dataResponse.data.bean;



                        // Since we are pulling 20 records per page, then if the resulting size is lesser than 20, then it means
                        // no more records are available.
                        if(list.length <20)
                        {
                          this.allNotificationsFetched = true;
                        }

                        list.forEach((item) => {

                            if(item.createdOn !== null)
                            {
                                item.createdOn = new Date(item.createdOn).toLocaleString()
                                                                         .slice(0, 17)
                                                                         .replace(',', '')
                                                                         .replace('T',' ');
                            }

                            item.hide = false;
                            this.notificationsList.push(item);
                        });

                        //If there are more records to fetch, then start observing the loadMore-intObsrv-trigger.
                        if(!this.isNotificationListIntersectionObserverFired)
                        {
                          this.initLoadMoreIntersectionObserverInNotificationsList();
                          this.isNotificationListIntersectionObserverFired = true;
                        }
                  })
                  .catch((error) => {
                      console.error("fetchNotifications Error : ", error);
                  });
          }
          catch(error)
          {
              console.error("Unpaginated List data fetch error : ", error);
          }
      },
      initLoadMoreIntersectionObserverInNotificationsList(){

          //console.log("bus = ", bus);

          this.observer = new IntersectionObserver((entries) => {
              if (entries[0].intersectionRatio <= 0) { // If not in view
                  console.log("Load more is not in view. " + "#" + this.observerElement);
              }
              else{
                  console.log("Load more is in view. " + "#" + this.observerElement);
                  //observer.unobserve(document.getElementById(observerElement));

                  //Increment the current page number
                  this.currentPageNumberOfNotifications += 1;
                  this.fetchNotifications();
              }
          },
          {
            root: null,
            rootMargin: "0px",
          });

          setTimeout(()=>{
              this.observer.observe(document.getElementById("isNotificationsListIntersectionObserver"));
          }, 1000);

      },
      updateNotificationsList(data){
        console.log("Received new notification from real-time : ", data);
        data.isNew = true;
        this.notificationsList.unshift(data);
      }
    },
    created: function() {},
    computed: {
      storeCounter: function() {
        //console.log('Accessing vuex store ', this.$store)
        //console.log('Store data accessed from home page : ' + this.$store.getters.count)
      }
    },
    mounted: function() {

      bus.on("new-notification", (data)=>this.updateNotificationsList(data));

      console.log("Fetching notifications..");
      this.fetchNotifications();
    },
    watch: {
    }
  }

</script>


